import React, { Component, Suspense } from "react";
import { connect } from "react-redux";
import { Table } from "react-bootstrap";
import { kFormatter, dateFormat } from "../../utils"; 
import { paginate } from '../../../../../CommonComponents/Paginate';
import Paggination from "../common/popup/paggination";
import PopupSearch from "../common/popup/popupSearch";
import ConnectButton from "../common/connectButton"; 
import MultiPostMedia from "../common/multiPostMedia";
import AuthorInfo from "../../content/components/authorInfo";
import DataNotFound from "../common/DataNotFound";
const LeaderbordSkeleton = React.lazy(() =>
  import("../skeleton/leaderbordSkeleton")
);


class AuthorpostsPopup extends Component {
  state = {
    fetchRecord: null,
    isLoading: true,
    total: 0,
    limit: 10,
    offset: 0,
    paginateData: {},
    currentPage: 1,
    maxPage: 10,
    username: null
  }

  componentDidMount() {
     const { total } = this.props;
     const { limit, currentPage, maxPage } = this.state;
      if (total)  this.setState({ total: total, paginateData: paginate(total, currentPage, limit, maxPage) }, () => this.onLoadFilterData())
   }


  onLoadFilterData = (isSearch=0) => { 
    
    const { analyticwall, total, moreData, isDashboard, selectedWalls , startDate, endDate} = this.props;
    const { selectedWallFeed, dateFilter, selectedFeed } = analyticwall;
    const { currentPage, limit, offset, username, maxPage } = this.state;

    let wallData = isDashboard ? selectedWalls: selectedWallFeed?.wall?.id;
    let startDateNew = isDashboard ? startDate :dateFilter.startDate;
    let endDateNew = isDashboard ? endDate:dateFilter.endDate;

    let feedIDs = [];
    if (selectedFeed && selectedFeed.length > 0) {
      selectedFeed.map((fItem) => {
        feedIDs.push(fItem.value);
      })
    }

    this.setState({ isLoading: true })
       moreData(wallData, dateFormat(startDateNew), dateFormat(endDateNew), feedIDs.join(), isSearch ? username && username.length > 0 ? total : limit  :  offset, isSearch ? username.length > 0 ? 0 : offset : limit, 0, username)
      .then((response) => {
      const { responseData } = response.data;

        if(!isSearch) this.setState({ fetchRecord: responseData.data, isLoading: false })
          else
        this.setState({ total: responseData.total, paginateData: paginate(responseData.total, currentPage, limit, maxPage), fetchRecord: responseData.data, isLoading: false })
    })
      .catch((error) => {
        console.error("LAYOUT ISSUE", error)
      });
   }   


  managePagging=(pageClick,currentPage=0,total=0,page=0,limit=0,maxPage=0,offset=0)=>{
      if(pageClick==1)
          this.setState({ currentPage: currentPage });
          else if(pageClick==2)
          this.setState(
            { paginateData: paginate(total, page, limit, maxPage), offset: offset },
            () => this.onLoadFilterData()
        );
         else if(pageClick==3)
          this.setState(
            {
                currentPage: page,
                paginateData: paginate(total, page, limit, maxPage),
                offset: offset,
            },
            () => this.onLoadFilterData()
            );
        else if(pageClick==4)
        this.setState(
          {
              limit: limit,
              paginateData: paginate(total, currentPage, limit, maxPage),
              offset: offset,
          },
          () => this.onLoadFilterData()
        );
     }


  updateSearchText = (value) => {
    this.setState({ username: value, currentPage: 1 }, () => this.onLoadFilterData(1))
  }
  
  render() {
    const {  analyticsStatus, isPdfDownloading, maxPage, hideFollwers  } = this.props;
    const { isLoading, fetchRecord, total, limit, paginateData, currentPage } = this.state;

    return (
      <div className="influential_contributors_custom_modal px-5">
        <PopupSearch isPdfDownloading={isPdfDownloading} analyticsStatus={analyticsStatus} updateSearchText={this.updateSearchText}/>
        <div className="influential-data-list sw_theme ">
        <Table responsive>
                <thead>
                  <tr>
                    <th style={{ width: "10%" }}>
                      S No.
                    </th>
                    <th style={{ width: "25%" }}>
                      Name
                    </th>
                   { ![1].includes(hideFollwers) ?  
                    <th style={{ width: "15%" }}>
                      Followers
                    </th> : null }
                    <th style={{ width: "10%" }}>
                      Posts
                    </th>
                    <th style={{ width: "25%" }}>
                      Recent Posts
                    </th>
                    <th style={{ width: "15%" }}></th>
                  </tr>
                </thead>
                <tbody>
                    {
                      isLoading ? <Suspense fallback={<></>}> <LeaderbordSkeleton/> </Suspense> : 
                      (fetchRecord && fetchRecord.length > 0) ? fetchRecord.map((mItem, mIndex) =>  <tr>
                      <td>{mIndex+1}</td>
                      <td>
                        <AuthorInfo picture={mItem.image} authorUsername={mItem.author.username} isIcon={mItem.network.icon} authorName={mItem.author.name}  networkId={mItem.networkId} link={mItem.links}/>
                      </td>
                      { ![1].includes(hideFollwers) ?  
                      <td>
                        <p className={`mb-0 ${parseInt(mItem.author.follower) > 0 ? '' : 'd-none'}`}><strong className="text-dark">{kFormatter(Math.round(mItem.author.follower))}</strong> {mItem.author.follower && parseInt(mItem.author.follower) > 1 ? `Followers` : `Follower`}</p>
                      </td> : null }
                      <td>
                        <p className={`mb-lg-3 mt-sm-2 mb-0 ${parseInt(mItem.postCount) > 0 ? '' : 'd-none'}`}><strong className="text-dark">{kFormatter(Math.round(mItem.postCount))}</strong> {mItem.postCount && parseInt(mItem.postCount) > 1 ? `Posts` : `Post`}</p>
                      </td>
                      <td>
                        <MultiPostMedia postsData={mItem.postsData} /> 
                      </td>
                      <td>
                        <div className="conn_status connnected">
                          <ConnectButton link={mItem.links} network={mItem.network.id} />
                        </div>
                      </td>
                    </tr>
                      ) : <tr>
                          <td colspan={6}> <DataNotFound />
                       </td></tr>
                   }
                 
                </tbody>
              </Table>

        </div>
          <Paggination isLoading={isLoading} total={total} paginateData={paginateData} maxPage={maxPage} currentPage={currentPage} limit={limit}  managePagging={this.managePagging} />
       </div>
    );
  }
}
const mapStateToProps = (state) => {   
  return {
    analyticwall: state.analyticwall,
  }
}

export default connect(mapStateToProps)(AuthorpostsPopup);